@import url(https://fonts.googleapis.com/css?family=Lato);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #282c34;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

table {
    margin: 0 auto;
    max-width: 90%;
    display: inline-flex;
    font-size: 18px;
  }
  
  table td {
    border: 2px black solid;
    border-radius: 5px;
    padding: 2%;
    margin: 1%;
    max-width: 15%;
    min-width: 10vw;
    background-color: rgb(67, 101, 128);
  }
  
  .title-row td {
    font-weight: bolder;
    background-color: #494e5c;
  }
  
  .search {
    width: 100%;
    margin: 0 auto;
  }
  
  .search-bar {
    padding: 1%;
    width: 40vw;
    max-width: 400px;
  }
  .search > div {
    display: block;
    justify-content: center;
    width: 70%;
    margin: 0 auto;
  }
  
  .search > div > button {
    padding: 1%;
    margin: 1%;
    height: 4vh;
    font-size: 0.75rem;
    line-height: 0.75em;
    width: 50%;
    max-width: 400px;
    border-radius: 7px;
  }
  
  .search > div > form > input {
    text-align: center;
    font-size: 0.85rem;
    line-height: 0.8em;
  }
  
  select {
    text-transform: capitalize;
    margin: 1%;
    width: 10%;
    font-size: 0.85rem;
  }
  
  .pagination {
    margin: 2%;
    list-style-type: none;
    max-width: 80%;
    max-height: 10%;
    text-align: center;
  }
  
  .pagination a {
    color: black;
    background-color: rgb(67, 101, 128);
    padding: 10px 15px;
    margin: 10px;
    float: left;
    border-radius: 5px;
  }
  
  .pagination a:hover {
    background: #494e5c;
    color: white;
  }
  
  h1 {
    margin: 0;
    padding: 10px;
  }

  .footer {
    width: 100%;
    height: 100%;
    background-color: #282c34;
    color: white;
    display: block;
    text-align: center;
  }
  
  .App {
    background-color: #282c34;
    color: white;
  }
  
  .image {
    width: 9vw;
    height: 17vh;
  }

  @media (max-width: 1660px) {
    .image {
      width:9vw;
      height: 15vh;
    }
  } 

  @media (max-width: 1440px) {
    .image {
      width:8vw;
      height: 11vh;
    }
  }

  @media (max-width: 1142px) {
    .image {
      width:8vw;
      height: 10vh;
    }
  }

  @media (max-width: 950px) {
    .image {
      width:8vw;
      height: 6vh;
    }
  }

  @media (max-width: 850px) {
    .image {
      width:8vw;
      height: 6vh;
    }
  }
  @media (max-width: 450px) {
    .image {
      width:10vw;
      height: 8vh;
    }
  }

* {
    box-sizing: border-box;
}


#app {
    width: 100%;
    max-width: 768px;
}

form {
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin-bottom: 15px;
}

form input {
    border: 2px solid #aaa;
    color: #333;
    font-size: 14px;
    font-family: Lato;
    padding: 10px 15px;
    height: 40px;
    min-width: 300px;
}

form button {
    border: 1px solid #aaa;
    background-color: #aaa;
    color: #fff;
    padding: 0 12px;
    height: 40px;
}

form button:focus {
    outline: none;
}

form button:active {
    opacity: 0.7;
}

.movie-card-container {
    width: 70%;
    border-radius: 10px;
    box-shadow: 0 16px 24px 2px rgba(0, 0, 0, 0.14),
        0 6px 30px 5px rgba(0, 0, 0, 0.12), 0 8px 10px -7px rgba(0, 0, 0, 0.2);
    overflow: hidden;
    display: flex;
    min-height: 420px;
    margin: 0 auto 50px;
    margin-top: 50px;
}

.image-container {
    display: flex;
    flex: 4 1;
    align-items: center;
    justify-content: center;
}

.image-container img {
    width: 225px;
    height: 225px;
}

.container-wrap button {
  font-size: 20px;
  border-radius: 5px;
  padding: 5px 15px;
  color:#2c7a13;
  background-color: #393c46;
}

.bg-image {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: top left;
    width: 100%;
    height: 100%;
}

.movie-info {
    display: flex;
    flex: 3 1;
    flex-direction: column;
    align-items: flex-end;
    text-align: right;
    padding: 30px 20px 30px 0;
    z-index: 1;
}

.movie-info h2 {
    text-transform: uppercase;
    padding: 0 0 10px;
    border-bottom: 3px solid rgba(0, 0, 0, 1);
}

.movie-info h1 {
    font-weight: bold;
    font-size: 36px;
}

.movie-info h4 {
    margin: 20px 0 10px;
}

.movie-info p {
    font-size: 20px;
    margin: 10px 0;
    width: 100%;
}

.movie-info .tags-container {
    position: relative;
    font-size: 22px;
}

.movie-info .genres-container span{
    border:2px solid #6c64b7;
    border-radius: 4px;
    display: inline-block;
    font-size: 12px;
    padding: 3px 10px;
    margin: 5px;
}

.movie-info .tags-container span {
    border:2px solid #b17474;
    border-radius: 4px;
    display: inline-block;
    font-size: 18px;
    padding: 3px 10px;
    margin: 5px;
}

footer {
    background-color: #222;
    color: #fff;
    font-size: 14px;
    bottom: 0;
    position: fixed;
    left: 0;
    right: 0;
    text-align: center;
    z-index: 2;
}

footer p {
    margin: 10px 0;
}

footer i {
    color: red;
}

footer a {
    color: #3c97bf;
    text-decoration: none;
}

@media (max-width: 768px) {
    .image-container {
        flex: 5 1;
    }
}


@media (max-width: 800px) {
    .movie-card-container {
        flex-direction: column;
    }

    .image-container {
        -webkit-clip-path: none;
                clip-path: none;
        flex: 1 1;
        height: 400px;
    }

    .movie-info {
        align-items: flex-start;
        flex: 1 1;
        text-align: left;
        padding: 30px 20px;
    }

    .movie-info .tags-container,
    .movie-info p {
        width: 100%;
    }

    .movie-info h2 {
        margin:auto;
    }
}
.App {
  text-align: center;
  background-color: #282c34;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

